.container {
  max-width: 1185px;
  margin: 0 auto;
  padding: 0 0.9375rem;
  position: relative;
  height: inherit;
}

body {
  font-size: 1rem;
  font-family: Circe;
}

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 100vh;
}

.main {
  flex: 1 1 auto;
  background: url("../assets/main_bg.png") 0 50%/cover no-repeat;
}

.section__title {
  font-size: 3.4375rem;
  color: #d1370b;
  text-align: center;
}