.raspil__container {
  padding-top: 33px;
  width: 100%;
  height: 100vh;
  display: flex;
}

.raspilMenu {
  width: 263px;
  margin-right: 30px;
}
.raspilMenu__item {
  display: flex;
  align-items: center;
  height: 37px;
  font-size: 16px;
  cursor: pointer;
  transition: all 300ms ease;
}
.raspilMenu__item:hover {
  color: #d54c2f;
}
.raspilMenu__item + .raspilMenu__item {
  border-top: 1px solid #e8dccf;
}

.services {
  width: 847px;
}
.services__title {
  height: 112px;
  display: flex;
  align-items: center;
  font-size: 54px;
  color: #d54c2f;
}
.services__text {
  width: 820px;
}
.services__tabs {
  margin-top: 44px;
}
.services__product {
  display: flex;
  flex-wrap: wrap;
  row-gap: 53px;
  column-gap: 22px;
}

.breadcrumbs {
  height: 37px;
  border-bottom: 1px solid #e8dccf;
  display: flex;
  align-items: center;
  gap: 5px;
}
.breadcrumbs__item {
  font-size: 18px;
}
.breadcrumbs__item:not(:last-child) {
  color: #d54c2f;
}
.breadcrumbs__item:last-child {
  color: #b9b9b9;
}

.tabs__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e8dccf;
}

.tabs__item {
  position: relative;
  padding-bottom: 12px;
  font-size: 22px;
  color: #b9b9b9;
  cursor: pointer;
}
.tabs__item-active {
  color: #d54c2f;
}
.tabs__item-active::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 4px;
  background: #d54c2f;
}

.product__item {
  margin-top: 25px;
  height: 266px;
  width: 195px;
}
.product__img {
  height: 159px;
  width: 100%;
  border-radius: 10px;
  background: burlywood;
}
.product__title {
  margin-top: 16px;
  width: 152px;
  font-size: 20px;
}
.product__code {
  margin-top: 23px;
  font-size: 16px;
  color: #b9b9b9;
}