.promo {
  height: 30.9375rem;
}
.promo__slider {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.promo__left {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1;
}
.promo__right {
  position: absolute;
  top: 0;
  right: 0;
  width: 51%;
  height: 100%;
  z-index: 1;
}
.promo__wrapper {
  position: absolute;
  top: -21.25rem;
  left: 8rem;
  z-index: 8;
}
.promo__title {
  font-weight: bold;
  font-size: 3.4375rem;
  text-transform: uppercase;
}
.promo__descr {
  margin-top: 2.5rem;
  font-size: 1.5rem;
  max-width: 25.9375rem;
  margin-bottom: 2.3125rem;
}
.promo__button {
  display: inline-block;
  font-size: 1.125rem;
  height: auto;
  width: auto;
  padding: 1rem 1.25rem;
}

.slider__button {
  position: absolute;
  top: 50%;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: #d1370b;
  z-index: 10;
  transition: all 0.3s ease;
  cursor: pointer;
}
.slider__button img {
  display: block;
  width: 0.75rem;
  height: 1.5rem;
  margin: 0 auto;
}
.slider__button:hover {
  background: #ed4e23;
}
.slider__dots {
  justify-content: center;
  display: flex;
  gap: 0.3125rem;
  margin-top: 1.6875rem;
}
.slider__dot {
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 50%;
  background: #e9dbc2;
}
.slider__dot._active {
  background: #ed4e23;
}

.next {
  right: 0.9375rem;
}

.prev {
  left: 0.9375rem;
}

.advantages {
  margin-top: 4.3125rem;
}
.advantages__list {
  margin-top: 6.6875rem;
  display: flex;
  padding: 0 1.4375rem;
  justify-content: space-between;
}
.advantages__item {
  max-width: 11.25rem;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.advantages__iconWrapper {
  height: 100%;
  max-height: 5.9375rem;
}
.advantages__icon {
  object-fit: contain;
  object-position: center;
  width: 100%;
  height: 100%;
}
.advantages__descr {
  margin-top: 1.125rem;
}

.prices {
  margin-top: 5.3125rem;
}
.prices__container {
  background: #fff;
  box-shadow: 0px 15px 40px 0px rgba(0, 0, 0, 0.22);
  border-radius: 0.375rem;
  padding: 0 1.5rem;
}
.prices__title {
  padding: 3.125rem 0;
}
.prices__items {
  display: flex;
  padding-bottom: 3.125rem;
}
.prices__item {
  background: #ede7e2;
  border-radius: 0.375rem;
}
.prices__itemPhoto {
  height: 9.75rem;
  width: 22.5rem;
}
.prices__itemPhoto img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 0.375rem;
}