.header {
  background: url("../assets/header_bg.png") 0/cover no-repeat;
}

.infoTop {
  background: url("../assets/topMenuBG.png");
  font-size: 1.125rem;
  opacity: 0.5;
  padding: 0.625rem 0;
}
.infoTop__container {
  display: flex;
  justify-content: space-between;
}

.infoBottom {
  font-weight: normal;
}
.infoBottom__logo {
  display: block;
  max-width: 21.25rem;
  max-height: 5.3125rem;
  flex: 0 2 24%;
  margin-right: 3rem;
}
.infoBottom__logo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.infoBottom__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  gap: 0.9375rem;
}
.infoBottom__contacts {
  width: 12.1875rem;
}
.infoBottom__number {
  font-size: 1.4375rem;
  font-weight: 700;
}
.infoBottom__number + .infoBottom__number {
  margin-top: 0.3125rem;
}

.button {
  height: 2.6875rem;
  padding: 0 1.125rem;
  background: #d1370b;
  border: 0.0625rem solid #d1370b;
  border-radius: 0.5rem;
  color: #fff;
  text-transform: uppercase;
  display: flex;
  gap: 0.375rem;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.0625rem;
  font-weight: bolder;
}

.secondary {
  background: transparent;
  color: #d1370b;
}

.buttons__group {
  display: flex;
  gap: 0.9375rem;
}

.menu__item {
  padding: 0.8125rem 0.9375rem;
  transition: all 0.3s ease;
  position: relative;
  cursor: pointer;
  border-top-left-radius: 0.375rem;
  border-top-right-radius: 0.375rem;
}
.menu__item:hover {
  color: #d1370b;
  box-shadow: 0px 15px 40px 0px rgba(0, 0, 0, 0.22);
  background-color: rgba(255, 254, 255, 0.7);
}
.menu__item:hover .submenu__list {
  visibility: visible;
  opacity: 1;
}
.menu__list {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  justify-content: space-between;
  height: 3.75rem;
  border-top: 0.09375rem solid #eee3dc;
}

.submenu__list {
  box-shadow: 0px 15px 45px 0px rgba(0, 0, 0, 0.22);
  border-radius: 0.375rem;
  border-top-left-radius: 0rem;
  position: absolute;
  top: 2.75rem;
  left: 0;
  padding: 3.4375rem 0.9375rem;
  background-color: rgba(255, 254, 255, 0.7);
  width: 20.3125rem;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 10;
}
.submenu__item {
  color: black;
  transition: all 0.3s ease;
  font-weight: regular;
}
.submenu__item:hover {
  color: #ed4e23;
}
.submenu__item + .submenu__item {
  margin-top: 1.25rem;
}