.footer {
  background: url("../assets/footer_bg.png");
  height: 20.625rem;
  padding-top: 3.875rem;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  gap: 0.9375rem;
}
.footer__info {
  flex: 0 0 25rem;
}
.footer__logo {
  width: 12.1875rem;
  height: 3.125rem;
}
.footer__logo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.footer__copyright {
  margin-top: 0.8125rem;
  font-size: 1rem;
}
.footer__email {
  margin-top: 0.625rem;
  text-align: end;
  margin-bottom: 2.75rem;
}

.socials {
  display: inline-block;
  margin-top: 2.5rem;
  display: flex;
  gap: 0.5rem;
}
.socials__item {
  background-color: #d1370b;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
}
.socials__item:hover {
  background-color: #ed4e23;
}

.categories {
  font-weight: bold;
  color: #d1370b;
  line-height: 1.5;
}
.categories__item {
  margin: 0.5rem 0;
  transition: all 0.3s ease;
}
.categories__item:hover {
  color: #ed4e23;
}